<template>
  <div class="container">
    <h2><i18n path="common.install"></i18n></h2>
    <hr class="my-2">
    <p><i18n path="common.guide.useNpm"></i18n></p>

    <showcode :trigger="false">
    <pre class="brush:js">
    npm i v-suggest --save
    </pre>
    </showcode>
    <p><i18n path="common.guide.include"></i18n></p>

    <showcode :trigger="false">
    <pre class="brush:js">
    // Global install as a vue plugin
    import Vue from 'vue'
    import vSuggest from 'v-suggest'
    Vue.use(vSuggest)

    // Import Suggest as a local component
    import { Suggest } from 'v-suggest'
    export default {
      components: {
        'v-suggest': Suggest
      }
    }
    </pre>
    </showcode>

    <h2 class="mt-5"><i18n path="common.deploy"></i18n></h2>
    <hr class="my-2">
    <showcode :trigger="false">
    <script type="syntaxhighlighter" class="brush:html">
    <![CDATA[
    <template>
      <v-suggest :data="list" show-field="name"></v-suggest>
    </template>

    &lt;script&gt;
    export default {
      data () {
        return {
          list: [
            { id: 1, name: 'Chicago Bulls', desc: '芝加哥公牛' },
            { id: 2, name: 'Cleveland Cavaliers', desc:'克里夫兰骑士' },
            { ... }
          ]
        }
      }
    }
    &lt;/script&gt;
    ]]>
    </script>
    </showcode>

    <h2 class="mt-5">
      <i18n path="common.example"></i18n>
      <small class="ml-2">
        <a href="https://codepen.io/terry05/pen/KKPQwxP" target="_blank">
          <i18n path="common.codePen"></i18n>
        </a>
      </small>
    </h2>

    <div class="card p-5 shadow-sm mt-3">

    <div class="">
      <h5><i18n path="suggest.demo.singleColumn"></i18n></h5>
      <pre class="p-3 bg-light">{{JSON.stringify(value1, null, 4)}}</pre>
      <p>
        <v-suggest :data="sample" show-field="name" v-model="value1">
        </v-suggest>
      </p>
      <showcode>
        <script type="syntaxhighlighter" class="brush:html;">
        <![CDATA[
        <v-suggest :data="sample"
          show-field="name"
          v-model="value1">
        </v-suggest>
        ]]>
        </script>
      </showcode>
    </div>

    <div class="mt-5">
      <h5><i18n path="suggest.demo.custom"></i18n></h5>
      <pre class="p-3 bg-light">{{JSON.stringify(value2, null, 4)}}</pre>
      <p>
        <v-suggest :data="sample" :show-field="resultFormat" v-model="value2">
        </v-suggest>
      </p>
      <showcode>
        <script type="syntaxhighlighter" class="brush:html;">
        <![CDATA[
        <v-suggest :data="sample"
          :show-field="resultFormat"
          v-model="value2">
        </v-suggest>

        //script
        methods: {
          resultFormat(row){
            return row.name+' ('+row.desc+')';
          }
        }
        ]]>
        </script>
      </showcode>
    </div>

    <div class="mt-5">
      <h5>Placeholder <small><i18n path="suggest.demo.placeholder"></i18n></small></h5>
      <p>
        <v-suggest :data="sample" show-field="name" placeholder="Type something..." >
        </v-suggest>
      </p>
      <showcode>
        <script type="syntaxhighlighter" class="brush:html;">
        <![CDATA[
        <v-suggest :data="sample" placeholder="Type something..."></v-suggest>
        ]]>
        </script>
      </showcode>
    </div>

    <div class="mt-5">
      <h5><i18n path="suggest.demo.disabled"></i18n></h5>
      <p>
        <button type="button" class="btn btn-outline-secondary" @click="disabled = !disabled">Toggle disabled</button>
      </p>
      <p>
        <v-suggest :data="sample" show-field="name" v-model="valDisabled" :disabled="disabled" >
        </v-suggest>
      </p>
      <showcode>
        <script type="syntaxhighlighter" class="brush:html;">
        <![CDATA[
        <v-suggest :data="sample" :disabled="true"></v-suggest>
        ]]>
        </script>
      </showcode>
    </div>

    <div class="mt-5">
      <h5><i18n path="suggest.demo.slot"></i18n></h5>
      <p>
        <v-suggest :data="sample" >
          <template #default="{ row }">
            <div>
              <strong v-text="row.name"></strong>
              <small class="ml-2" v-text="row.abbr"></small>
            </div>
            <div class="text-muted" v-text="row.desc"></div>
          </template>
        </v-suggest>
      </p>
      <showcode>
        <script type="syntaxhighlighter" class="brush:html;">
        <![CDATA[
        <v-suggest :data="sample">
          <!-- scoped slot -->
          <template #default="{ row }">
            <div>
              <strong v-text="row.name"></strong>
              <small class="ml-2" v-text="row.abbr"></small>
            </div>
            <div class="text-muted" v-text="row.desc"></div>
          </template>
        </v-suggest>
        ]]>
        </script>
      </showcode>
    </div>

    <div class="mt-5">
      <h5><i18n path="suggest.demo.limit"></i18n></h5>
      <p>
        <v-suggest :data="sample" :max-length="5">
        </v-suggest>
      </p>
      <showcode>
        <script type="syntaxhighlighter" class="brush:html;">
        <![CDATA[
        <v-suggest :data="sample" :max-length="5"></v-suggest>
        ]]>
        </script>
      </showcode>
    </div>

    <div class="mt-5">
      <h5><i18n path="suggest.demo.raw"></i18n></h5>
      <pre class="p-3 bg-light">{{JSON.stringify(valCallback, null, 4)}}</pre>
      <p>
        <v-suggest :data="sample" show-field="name" @values="values">
        </v-suggest>
      </p>
      <showcode>
        <script type="syntaxhighlighter" class="brush:html;">
        <![CDATA[
        <v-suggest :data="sample" @values="values"></v-suggest>

        //script
        methods: {
          values(data){
            console.log(data);
          }
        }
        ]]>
        </script>
      </showcode>
    </div>
    </div>

    <h2 class="mt-5"><i18n path="common.options"></i18n></h2>
    <table class="table table-bordered table-striped option-table">
      <thead>
        <tr>
          <th><i18n path="common.tbTitle.name"></i18n></th>
          <th><i18n path="common.tbTitle.type"></i18n></th>
          <th><i18n path="common.tbTitle.desc"></i18n></th>
          <th><i18n path="common.tbTitle.required"></i18n></th>
          <th><i18n path="common.tbTitle.default"></i18n></th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td>data</td>
          <td>array</td>
          <td><i18n path="suggest.option.data"></i18n></td>
          <td>true</td>
          <td></td>
        </tr>
        <tr>
          <td>show-field</td>
          <td>string|function</td>
          <td><i18n path="suggest.option.showField"></i18n></td>
          <td>false</td>
          <td>name</td>
        </tr>
        <tr>
          <td>placeholder</td>
          <td>string</td>
          <td><i18n path="suggest.demo.placeholder"></i18n></td>
          <td>false</td>
          <td></td>
        </tr>
        <tr>
          <td>disabled</td>
          <td>boolean</td>
          <td><i18n path="suggest.demo.disabled"></i18n></td>
          <td>false</td>
          <td>false</td>
        </tr>
        <tr>
          <td>max-length</td>
          <td>number</td>
          <td><i18n path="suggest.option.limit"></i18n></td>
          <td>false</td>
          <td>0</td>
        </tr>
      </tbody>
    </table>

    <h2 class="page-header"><i18n path="common.events"></i18n></h2>
    <table class="table table-bordered table-striped">
      <thead>
      <tr>
        <th><i18n path="common.tbTitle.param"></i18n></th>
        <th><i18n path="common.tbTitle.desc"></i18n></th>
        <th><i18n path="common.tbTitle.args"></i18n></th>
      </tr>
      </thead>
      <tbody>
      <tr>
        <td>values</td>
        <td><i18n path="suggest.event.values"></i18n></td>
        <td>Object</td>
      </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import nbaTeams from '@test/sample/nba-teams'

export default {
  data () {
    return {
      value1: '',
      value2: '',
      disabled: false,
      valDisabled: 'Oklahoma City Thunder',
      valCallback: null,
      sample: nbaTeams
    }
  },
  methods: {
    resultFormat (row) {
      if (row) {
        return row.name + ' (' + row.desc + ')'
      }
    },
    values (datas) {
      this.valCallback = datas
    }
  },
  mounted () {
    window.SyntaxHighlighter.highlight()
  }
}
</script>
